.text-section-main-div{
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 0px 0px 8px 1px #0000002b;
    color: #393e46;
    padding: 1rem 0;
    margin: 2rem 0;
}
.text-section-titles{
    font-size: 1.5rem;
    font-weight: 600;
    padding: 0 2rem;
}
.text-section-texts{
    font-size: 1.2rem;
    padding: 0 2rem;
}
@media only screen and (max-width: 768px){
    .text-section-titles{
        font-size: 1.3rem;
        padding: 0 1rem;
    }
    .text-section-texts{
        font-size: 1.1rem;
        padding: 0 1rem;
    }
}
@media only screen and (max-width: 480px){
    .text-section-titles{
        font-size: 1.2rem;
        padding: 0 0.7rem;
    }
    .text-section-texts{
        font-size: 1rem;
        padding: 0 0.7rem;
    }
}